// ************************* Imports *************************

@import 'antd/dist/antd.css';
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&family=Oswald&display=swap');

// Fonts: Montserrat-400,500,600
// Open Sans 400,600
// Oswald
