//*************************** Mixins **************************

@mixin flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}
@mixin align-center {
  display: flex;
  align-items: center;
}
@mixin demo-card($bg, $content, $bottom) {
  background-color: $bg;
  &::before {
    content: $content;
    position: absolute;
    bottom: $bottom;
    left: -1.5%;
    font-size: 3.5em;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
    color: rgba(59, 59, 59, 0.705);
  }
}
@mixin scrollBarStyle {
  padding-right: 0.3em;
  &::-webkit-scrollbar {
    width: 5px;
  }
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 10px;
  }
  &::-webkit-scrollbar-thumb {
    background: $primary-color;
    border-radius: 10px;
  }
  &::-webkit-scrollbar-thumb:hover {
    background: $primary-color;
  }
}

//************************** Variables *************************

