$orange: #ff6900;
$light-pink: #eebed3;
$light-blue: #aac6fc;
$light-sky: #d5f2fd;
$deep-sky: #10a3ff;
$light-green: #7fd6c2;
$light-yellow: #f6e049;
$success-green: #82d135;
$pesta: #ccedd2;
