//----------------------- Global Initialise Stylings -------------------
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 16px;
  line-height: 1.5;
}
h1 {
  font-size: 2.5rem;
  font-weight: 400;
  margin-bottom: 0;
  color: $dark-fade;
}
h2 {
  font-size: 2rem;
  color: $dark-fade;
  font-weight: 400;
  margin-bottom: 0;
}
h3 {
  font-size: 1.75rem;
  color: $dark-fade;
  font-weight: 500;
  margin-bottom: 0.2em;
}
h4 {
  font-size: 1.5rem;
  color: $dark-fade;
}
h5 {
  font-size: 1rem;
  color: $dark-fade;
}
p {
  color: $dark-fade;
}

section {
  width: 95%;
  margin: 1em auto;
  padding: 1em;
  position: relative;
  overflow: hidden;
  h2 {
    margin-bottom: 1em;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      top: -2%;
      right: 30%;
      height: 3px;
      width: 10%;
      background-color: $primary-color;
    }
    &::after {
      content: '';
      position: absolute;
      top: -26%;
      right: 20%;
      height: 3px;
      width: 20%;
      background-color: $blue;
    }
  }
}
button {
  padding: 0.3em 1em;
  background-color: transparent;
  border-radius: 25px;
  transition: all 0.5s ease;
  cursor: pointer;
  &:focus {
    outline: transparent;
  }
}
a {
  color: inherit;
  &:hover {
    color: inherit;
    text-decoration: none;
  }
}
