//____________ Blog Component Design _______________

.blog-container {
  .ant-col {
    width: 100%;
  }
  .blog-card {
    width: 100%;
    padding: 0.5em;
    background: linear-gradient(145deg, #ebebeb, #ffffff);
    height: 400px;
    border: 1px solid rgb(212, 212, 212);
    margin-bottom: 0.5em;
    .card-top {
      height: 150px;
      background-size: cover;
      background-position: center;
      position: relative;
      span {
        background-color: $primary-color;
        color: $white;
        position: absolute;
        padding: 0.2em 0.5em;
      }
      span.about {
        bottom: 0;
        left: 0;
      }
      span.date {
        top: 1%;
        right: 1%;
        border-radius: 50%;
        text-align: center;
        height: 40px;
        width: 40px;
        font-size: 0.7em;
      }
    }
    .card-content {
      padding: 0.5em 1em;
      .sub-heading {
        color: #eb4b35;
      }
      .content {
        color: $fade;
        height: 80px;
      }
    }
    .card-bottom {
      display: flex;
      justify-content: space-between;
      span {
        @include align-center();
        color: $light-fade;
      }
      button {
        padding: 0 0.5em;
        border: 1px solid $primary-color;
        color: $primary-color;
        &:hover {
          background-color: $primary-color;
          color: $smoke;
        }
      }
    }
  }
}
