//______________ Modal Stylings ______________

.modal-container {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  backdrop-filter: blur(3px);
  @include flex-center();
  z-index: 99999;
  .ant-spin-dot-item {
    background-color: $white;
  }
  .pop-confirm {
    background-color: $white;
    padding: 2em 1em;
    text-align: center;
    img {
      width: 35%;
    }
    p {
      color: green;
    }
    h4 {
      color: #494949;
    }
    button {
      border: 1px solid $success-green;
      color: $success-green;
      margin: 0.5em;
      &:hover {
        background-color: $success-green;
        color: $white;
      }
    }
  }
  .pop-confirm.registration {
    width: 600px;
    img {
      width: 30%;
    }
  }
  .pop-confirm.logout {
    p {
      color: $alert;
      margin: 0;
    }
    button {
      width: 120px;
    }
    button.yes {
      border: 1px solid $alert;
      color: $alert;
      &:hover {
        background-color: $alert;
        color: $white;
      }
    }
  }
  .full-article-modal {
    width: 800px;
    .blog-card {
      height: 600px;
      .card-content {
        .content {
          height: 300px;
          overflow: auto;
          @include scrollBarStyle();
        }
      }
    }
  }
}

