//______________ Info Card Stylings __________________

.info-card {
  position: relative;
  width: 100%;
  height: auto;
  background-color: #e7e7de;
  border-radius: 4px;
  padding: 1em;
  z-index: 0;
  overflow: hidden;
  h3 {
    color: #00587a;
    font-weight: bold;
  }
  p {
    font-size: 17px;
    line-height: 20px;
    color: $fade;
  }
  .go-corner {
    @include flex-center();
    position: absolute;
    width: 32px;
    height: 32px;
    overflow: hidden;
    top: 0;
    right: 0;
    background-color: #008891;
    border-radius: 0 4px 0 32px;
  }
  .go-arrow {
    margin-top: -4px;
    margin-right: -4px;
    color: white;
    font-family: courier, sans;
  }
  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    top: -16px;
    right: -16px;
    background: #008891;
    height: 32px;
    width: 32px;
    border-radius: 32px;
    transform: scale(1);
    transform-origin: 50% 50%;
    transition: transform 0.6s ease-out;
  }
  &:hover {
    cursor: pointer;
    &::before {
      transform: scale(30);
    }
    p {
      transition: all 0.3s ease-out;
      color: #e7e7de;
    }
    h3 {
      transition: all 0.3s ease-out;
      color: #e7e7de;
    }
  }
}

