//------------------------ Navbar Stylings ---------------------------

.nav-bar {
  width: 100%;
  position: relative;
  z-index: 999;
  height: auto;
  font-size: 1.2em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5em 1em;
  background: rgba(255, 255, 255, 0.25) !important;
  box-shadow: 0 2px 22px 0 rgba(102, 102, 102, 0.37);
  backdrop-filter: blur(30px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid $primary-color;
  position: sticky;
  top: 0;
  .navbar-logo {
    @include align-center();
    img {
      width: 100px;
    }
  }
  .menu-icon {
    cursor: pointer;
    @include align-center();
    opacity: 0;
  }
  li {
    @include align-center();
  }
  .item-container {
    display: flex;
    list-style: none;
    margin: 0;
    .nav-items {
      @include align-center();
      margin: 0 1em;
      color: gray;
      position: relative;
      cursor: pointer;
      &:hover {
        color: $dark-fade;
      }

      .drop-down {
        width: 200px;
        position: absolute;
        top: 170%;
        left: 0;
        background-color: $white;
        border: 1px solid rgb(180, 180, 180);
        border-top: 0;
        list-style: none;
        padding: 0.5em 0.8em;
        li {
          margin: 0.2em 0;
        }
        .navlink {
          color: gray;
          @include align-center();
          .icon {
            margin: 0 0.3em 0 0;
          }
          &:hover {
            color: $dark-fade;
          }
        }
      }
      .drop-down.more-drop {
        width: 150px;
      }
    }
  }
}

