  margin: 1em auto 0.5em auto;
  h1 {
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    margin: 0;
  }
  h1::before {
    content: '';
    position: absolute;
    top: 2%;
    left: 30%;
    height: 3px;
    width: 10%;
    background-color: $primary-color;
  }
  h1::after {
    content: '';
    position: absolute;
    top: 0%;
    left: 35%;
    height: 3px;
    width: 10%;
    background-color: $blue;
  }
  .home-row {
    @include align-center();
    header {
      padding: 1em;
    }
  }
  .left-circle-border {
    height: 160px;
    width: 160px;
    border-radius: 50%;
    position: absolute;
    bottom: -12%;
    left: -2%;
    border: 2px solid $primary-color;
  }
  .circle-design {
    height: 150px;
    width: 150px;
    border-radius: 50%;
    position: absolute;
    bottom: -8%;
    left: -3%;
    background-color: $blue;
    z-index: 1;
  }
  .social-bar {
    position: absolute;
    bottom: 1%;
    right: 2%;
    list-style: none;
    li {
      margin: 1em 0;
      img {
        width: 1.3em;
      }
    }
  }
}

//_______ Moto Section ________

.moto {
  .title {
    background-color: $pink;
    border-radius: 17px;
    overflow: hidden;
    @include flex-center();
    h2 {
      transform: rotate(-90deg);
      &::before {
        content: '';
        position: absolute;
        top: -4%;
        right: 10%;
        height: 3px;
        width: 40%;
        background-color: $blue;
      }
    }
    .left-circle-border {
      left: 50%;
      bottom: -25%;
    }
  }
  .card {
    padding: 1em;
    margin: 0.5em auto;
    border-radius: 17px;
    display: flex;
    flex-direction: row;
    align-items: center;
    figure {
      width: 80%;
    }
  }
  #card-1 {
    @include demo-card($light-blue, '1', '80%');
  }
  #card-2 {
    @include demo-card($light-green, '2', '80%');
  }
  #card-3 {
    @include demo-card($light-yellow, '3', '80%');
  }
  #card-4 {
    @include demo-card($light-pink, '4', '80%');
  }
}

//____________ Proud Students Section ___________

.proud-students {
  border: 2px solid $light-blue;

  .swiper {
    img {
      width: 100%;
    }
  }
  .title {
    border-radius: 17px;
    @include flex-center();
    flex-direction: column;
    text-align: center;
    .side-img {
      width: 60%;
      margin: auto;
    }
    .animation {
      transform: scale(1.5);
      margin-top: 1em;
    }
    h2 {
      margin: 1em 0.5em;
      font-size: 1.8em;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        top: -2%;
        right: 30%;
        height: 3px;
        width: 10%;
        background-color: $primary-color;
      }
      &::after {
        content: '';
        position: absolute;
        top: -26%;
        right: 20%;
        height: 3px;
        width: 20%;
        background-color: $blue;
      }
    }
  }
  .left-circle-border {
    height: 160px;
    width: 160px;
    border-radius: 50%;
    position: absolute;
    bottom: -15%;
    left: 90%;
    background-color: $light-blue;
    border: none;
  }
}

//_______ Project Section _________

.projects {
  padding: 2em;
  background-color: $pink;
  border-radius: 17px;
  .title-container {
    @include flex-center();
    .title {
      width: 90%;
      text-align: center;
      h2 {
        margin-bottom: 0;
        position: relative;
        &::before {
          content: '';
          position: absolute;
          top: -2%;
          right: 30%;
          height: 3px;
          width: 10%;
          background-color: $primary-color;
        }
        &::after {
          content: '';
          position: absolute;
          top: -26%;
          right: 20%;
          height: 3px;
          width: 20%;
          background-color: $blue;
        }
      }
    }
  }
  .project-card {
    background: rgba(255, 255, 255, 0.55);
    box-shadow: 0 2px 32px 0 rgba(128, 128, 128, 0.37);
    backdrop-filter: blur(4px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    padding: 1em;
    margin: 0.5em;
    border-radius: 17px;
    .more {
      transform: scale(1.5);
      color: $fade;
      cursor: pointer;
    }
    .top {
      display: flex;
      justify-content: space-between;
      margin-bottom: 1em;
      .name {
        display: flex;
        align-items: center;
        h6 {
          margin: 0 0 0 0.5em;
        }
      }
      .more {
        font-size: 1.5em;
      }
    }
    h4 {
      margin: 0;
    }
    button {
      @include flex-center();
    }
  }
  .pc3,
  .pc4 {
    margin-top: 1em;
  }
  .left-circle-border {
    left: -2%;
    bottom: -4%;
  }
  .circle-design {
    left: -3%;
    bottom: -5%;
  }
}

//_______ Home Featured Videos Section _________

.videos {
  min-height: 75vh;
  padding-top: 2.5em;
  margin-top: 1.5em;
  background-color: #e5e5f7;
  background-image: radial-gradient(
    #a8a8a8 1.1500000000000001px,
    #e5e5f7 1.1500000000000001px
  );
  background-size: 23px 23px;
  h2 {
    width: 60%;
    margin: auto;
    text-align: center;
    margin-bottom: 0.5em;
  }
  .swiper-container {
    padding: 1em;
  }
  .video-cards {
    width: 100%;
    h5 {
      font-family: 'Montserrat', sans-serif;
      font-weight: bold;
      margin-bottom: 0;
    }
    .subtitle {
      font-weight: 500;
      color: $fade;
    }
    button {
      display: none;
    }
  }
}

.blog {
  header {
    width: 60%;
    margin: 1em auto 2em auto;
    text-align: center;
    h2 {
      margin-bottom: 0;
      &::before {
        width: 0;
      }
      &::after {
        width: 0;
      }
    }
    p {
      color: $fade;
    }
  }
  .blog-card {
    background: linear-gradient(145deg, #e6e6e6, #ffffff);
    box-shadow: 10px 10px 10px #d9d9d9, -10px -10px 10px #ffffff;
    border-radius: 25px;
    overflow: hidden;

    .cover-image {
      height: 130px;
      background-color: $orange;
    }
    .content {
      position: relative;
      padding: 3em 1em 1em;
      text-align: center;
      color: $fade;
      .profile-image {
        height: 85px;
        width: 85px;
        border-radius: 50%;
        margin: auto;
        background-color: $light-pink;
        position: absolute;
        top: -20%;
        left: 50%;
        transform: translateX(-50%);
      }
      .head {
        .badge {
          margin: 0 0.5em 0.5em;
        }
      }

      h4 {
        margin: 0;
      }
      .description {
        margin: 0;
      }
      .btn-container {
        position: relative;
        height: 15px;
        button {
          position: absolute;
          right: 0;
          background-color: #ec615b;
          padding: 0.3em;
          color: $smoke;
          border: none;
          @include flex-center();
          width: 25px;
          height: 25px;
          transition: width 0.2s ease;
          &:hover {
            width: 80px;
            &::before {
              content: 'more';
            }
          }
        }
      }
    }
  }
}
//--------------------------- Home-Page Stylings End -------------------

//-----------------------------Login Page Stylings ----------------------
