//--------------------------- Profile Page Stylings ----------------------------
.profile-page {
  //__________common styles__________
  section {
    width: 100%;
    margin: 1em auto;
    .container {
      position: relative;
    }
  }
  h4.section-head {
    margin-bottom: 1em;
    background-color: $pink;
    color: $dark-fade;
    padding: 0.3em;
    text-transform: uppercase;
    font-family: 'Oswald', sans-serif !important;
  }
  //________Individual section stylings________

  //======= Profile Top ========
  .top {
    background-color: $light-sky;
    padding: 1em 1em 0 1em;
    .container {
      display: flex;
      height: 39vh;
      .profile-picture {
        width: min-content;
        @include flex-center();
        margin: 1em 0;
        .dp {
          position: relative;
          height: 250px;
          width: 250px;
          border-radius: 50%;
          background-size: cover;
          border: 10px solid $white;
          box-shadow: -10px -10px 15px #c6efff, 10px 10px 15px #c6efff;
          .img-edit {
            position: absolute;
            bottom: 3%;
            right: 3%;
            background-color: $white;
            height: 35px;
            width: 35px;
            border-radius: 50%;
            @include flex-center();
            border: 1px solid $dark-fade;
          }
        }
      }
      .profile-details {
        width: 100%;
        position: relative;
        padding-left: 3em;
        header {
          padding: 1em 0;
          h3 {
            margin-bottom: 0;
          }
          .headline {
            color: $fade;
          }
          .role {
            width: min-content;
            margin: 1em 0;
            span {
              padding: 0.1em 0.5em;
              margin-right: 0.3em;
              background-color: rgb(255, 255, 255);
              border: 1px solid gray;
              opacity: 0.5;
            }
            span.active {
              background-color: $light-pink;
              border: 1px solid $primary-color;
              opacity: 1;
            }
          }
          .bio {
            p {
              margin-bottom: 0.3em;
              display: flex;
              align-items: center;
              color: $light-fade;
            }
          }
        }
        .action-bar {
          width: 98%;
          background-color: $white;
          position: absolute;
          bottom: 0;
          right: 0;
          .bar {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            border: 1px solid gray;
            border-bottom: none;
            padding: 0.3em 0;
            h4 {
              margin: 0;
            }
            button {
              width: 100%;
              height: 100%;
              border: none;
              border-radius: 0;
              background-color: $deep-sky;
              color: $white;
              @include flex-center();
            }
          }
          .btn-bar {
            padding: 0;
            border: none;
          }
        }
        .social-media {
          width: 25%;
          display: flex;
          justify-content: space-around;
          position: absolute;
          top: 0;
          right: 0;
          img {
            width: 25px;
            margin: 0 0.2em;
          }
        }
        .fresh-social-media {
          img {
            filter: grayscale(1);
            opacity: 0.5;
          }
        }
        .message {
          border-radius: 0;
          border: none;
          background-color: $white;
          position: absolute;
          top: 50%;
          right: 0;
          width: 170px;
          transform: translateY(-50%);
          @include flex-center();
        }
        .edit-btn {
          top: 35%;
        }
      }
    }
  }
  //=========== Bio and Achievement Section ===========
  .about-section {
    .container {
      h4 {
        border-bottom: 3px solid $light-green;
        padding-bottom: 0.5em;
        display: flex;
        align-items: center;
        img {
          margin-right: 0.5em;
        }
      }
      .bio {
        border: 1px solid #cccccc;
        padding: 1em;
        height: 100%;
        position: relative;
        p {
          color: $fade;
        }
      }

      .achievement-col {
        width: 100%;
      }
      .achievements {
        border: 1px solid #cccccc;
        height: 100%;
        width: 100%;
        padding: 0.5em;
        position: relative;
        .achievement {
          display: flex;
          justify-content: space-between;
          align-items: center;
          background-color: $smoke;
          margin-bottom: 0.3em;
          padding: 0 0.2em;
        }
        .more-btn {
          position: absolute;
          bottom: -10%;
          left: 0;
          width: 100%;
          padding: 0.3em;
          @include flex-center();
          background-color: $pesta;
          cursor: pointer;
        }
      }
    }
  }

  //============== Skills Section ==============

  .skills {
    padding-top: 0.5em;
    .container {
      h4 {
        background-color: $pink;
      }
      .skill-container {
        .skill-card {
          background-color: $smoke;
          padding: 1em;
          margin-bottom: 1em;
          .content {
            padding-left: 0.5em;
            .header {
              display: flex;
              justify-content: space-between;
              margin-bottom: 0.5em;
              h5 {
                margin: 0;
                font-size: 1.2em;
              }
              .icon-container {
                display: flex;
                align-items: center;
                .icon {
                  color: #ff6900;
                }
              }
            }
            p {
              height: 40px;
              color: $fade;
            }
          }
        }
      }
    }
  }

  //============= Profile Project Section =============

  .projects {
    background-color: transparent;
    padding-top: 0;
    .container {
      h4 {
        background-color: $pesta;
      }
      button {
        border-radius: 0;
        border: 1px solid $light-fade;
        margin-top: 1em;
        @include align-center();
        width: 100%;
        opacity: 0.5;
        &:hover {
          opacity: 1;
        }
      }
      .youtube-video-container {
        height: 178px;
      }
      .video-cards {
        margin: 0.3em 0;
        padding: 0.5em;
        box-shadow: none;
        background-color: #f6f6f6;
        border-radius: 0;
        border: 2px solid $smoke;
        h5 {
          font-family: 'Montserrat', sans-serif;
          margin: 0;
          font-weight: bold;
        }
        .subtitle {
          margin: 0 0 0.5em 0;
          font-weight: 500;
          color: $fade;
        }
        button {
          border: 1px solid #1a508b !important;
          color: #1a508b;
          border: none;
          opacity: 1;
          &:hover {
            background-color: #1a508b;
            color: $smoke;
          }
        }
      }
    }
  }
  //======== Profile Performance Section ==========
  .performance {
    .container {
      h4 {
        text-align: center;
        background-color: $blue;
        color: $white;
        border-radius: 25px;
        padding: 0.3em;
        width: 30%;
        margin: 0 auto 2em auto;
        box-shadow: 10px 10px 22px 0 rgba(102, 102, 102, 0.37);
      }
      .ant-steps-item-finish
        > .ant-steps-item-container
        > .ant-steps-item-tail::after {
        background-color: $primary-color;
      }
      .ant-steps-item-finish
        .ant-steps-item-icon
        > .ant-steps-icon
        .ant-steps-icon-dot {
        background: $primary-color;
      }
      .ant-steps-item-process
        .ant-steps-item-icon
        > .ant-steps-icon
        .ant-steps-icon-dot {
        background: $primary-color;
      }
    }
  }
  //==========profile blog section==========
  .blogs {
    .container {
      h4 {
        background-color: $light-blue;
      }
      //blog-card styling is global
    }
  }

  //_____________________ Profile Global Stylings ________________________

  //========= Fresh Section for New User ============
  .fresh-section {
    height: 185px;
    width: 100%;
    border: 2px dashed #cccccc;
    @include flex-center();
    cursor: pointer;
    header {
      text-align: center;
      opacity: 0.6;

      p {
        margin-bottom: 0.1em;
        font-size: 18px;
      }
      .fresh-icon {
        font-size: 22px !important;
        margin-bottom: 0.5em;
      }
    }
  }
  //========== Logout Option =================

  .more-options {
    position: absolute;
    bottom: 0;
    right: 2%;
    position: fixed;
    list-style: none;
    li {
      height: 40px;
      width: 40px;
      border-radius: 50%;
      margin-bottom: 0.5em;
      @include flex-center();
      transition: color 1s ease;
      cursor: pointer;
    }
    li.logout {
      border: 2px solid #d84b4b;
      color: #d84b4b;
      &::before {
        content: 'Logout';
        position: absolute;
        left: -30px;
        transform: translateX(-30px);
        color: $light-fade;
        opacity: 0;
        transition: all 1s ease;
      }
      &:hover {
        background-color: #d84b4b;
        color: $white;
        &::before {
          opacity: 1;
        }
      }
    }
  }

  //=============== Data Edit Button =================
  .edit-btn {
    height: 35px;
    width: 35px;
    border-radius: 50%;
    border: 1px solid $smoke;
    @include flex-center();
    position: absolute;
    top: 1%;
    right: 1%;
    transition: all 1s ease;
    cursor: pointer;
    &:hover {
      background-color: $smoke;
    }
  }
  .edit-btn.type2 {
    background-color: $smoke;
    right: 2%;
  }
  .edit-btn.edit-btn.type2.add {
    right: 6%;
  }

  //============= Profile Data Edit Modal =============

  .edit-modal {
    background-color: $white;
    width: 800px;
    padding: 1em;
    h4 {
      padding-bottom: 0.5em;
      border-bottom: 2px solid $light-green;
      margin-bottom: 1em;
      color: $fade;
      @include align-center();
      position: relative;
    }
    .form-container {
      width: 100%;
      max-height: 500px;
      overflow: auto;
      @include scrollBarStyle();
      .ant-row {
        padding-bottom: 0 !important;
        margin-bottom: 0.5em;
      }
      .fields-group {
        padding: 0.5em 0.2em;
        margin-bottom: 0.5em;
      }
    }
    .btn-container {
      text-align: right;
    }
    .close-btn {
      position: absolute;
      top: 50%;
      right: 2%;
      transform: translateY(-50%);
      font-size: 0.5em;
      height: 25px;
      width: 25px;
      border-radius: 50%;
      @include flex-center();
      background-color: $smoke;
    }
  }

  //===== profile-skill-edit ======
  .skill-edit-modal {
    width: 1000px;
  }

  //========= Profile More Achievements ========
  .more-achievement-modal {
    .achievement-bar-container {
      width: 100%;
      .achievement-bar {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: $smoke;
        padding: 0.3em;
        background-color: $primary-color;
        margin-bottom: 1em;
        a {
          @include align-center();
          .icon {
            margin-left: 0.5em;
          }
        }
        * {
          margin: 0;
        }
      }
    }
  }
  //_____________ Profile Global Stylings End ____________
}
//--------------------- Profile Page Stylings End ---------------------------

