        margin-bottom: 1em;
      }
    }
  }
  .vission {
    padding-bottom: 2em;
    overflow: hidden;
    .vission-card-container {
      .card {
        width: 80%;
        padding: 0.5em;
        margin: 1em 0;
        background-color: $smoke;
        display: flex;
        flex-direction: row;
        align-items: center;
        img {
          margin: 0 1em;
        }
        &:nth-child(2) {
          margin-left: auto;
          justify-content: flex-end;
        }
      }
    }
  }
}

//------------------- Error Page Stylings -----------------------------

.error-page {
  height: 85vh;
  width: 100%;
  @include flex-center();
  header {
    text-align: center;
    .container {
      width: 70%;
      margin: auto;
    }
    h2 {
      color: $deep-sky;
      font-family: 'Montserrat', sans-serif;
      font-weight: 500;
    }
    p {
      color: $dark-fade;
    }
    button {
      border: 1px solid $deep-sky;
      color: $deep-sky;
      &:hover {
        background-color: $deep-sky;
        color: $white;
      }
    }
  }
}
//-----------------------Help Page Stylings ---------------------------------
.help {
  .top {
    padding: 1em 0;
    height: auto;
    background-color: #97dcdf;
    text-align: center;
    .ant-col {
      width: 100%;
    }
    img {
      width: 250px;
      margin: auto;
    }
    header {
      h1,
      p {
        color: #257679;
        font-family: 'Montserrat', sans-serif;
        margin: 0.2em auto;
        font-weight: 500;
      }
      h1 {
        color: #2a8488;
        font-weight: 600;
      }
    }
  }
  .content {
    margin: 2em auto;
    padding-bottom: 2em;
    h3 {
      color: #2a8488;
    }
    button {
      background-color: #2a8488;
    }
  }
}
//-------------------Feedback Page Styling----------------------------------
.feedback {
  min-height: 93vh;
  background: #7f7fd5;
  background: -webkit-linear-gradient(to right, #91eae4, #86a8e7, #7f7fd5);
  background: linear-gradient(to right, #91eae4, #86a8e7, #7f7fd5);
  @include flex-center();
  .space {
    width: 700px;
    background-color: $white;
    padding: 2em 2em 1em 2em;
    border-radius: 22px;
    box-shadow: 30px 30px 80px rgba(0, 0, 0, 0.267);
    h3 {
      color: #8189da;
    }
    p {
      color: #86a7e6;
    }
    button {
      background: #7f7fd5;
    }
    .ratings-field {
      span {
        transform: scale(2);
        margin: 0 1em;
      }
    }
  }
}
//------------------ Forgot Password Page Stylings -----------------------------

.forget-password-page {
  width: 100%;
  min-height: 100vh;
  background-color: #2993eb;
  @include flex-center();
  .container {
    @include flex-center();
    text-align: center;
    header {
      text-align: center;
      color: #f5f8f5;
      padding-top: 2em;
      h2 {
        color: #f5f8f5;
      }
      .input-div {
        .my-form {
          width: 70%;
          margin: auto;
          text-align: left;
          .ant-form-item-explain {
            color: $smoke;
          }
        }
        input {
          border: none;
          border-radius: 17px;
          padding: 0.3em 0.5em;
          color: $fade;
          &:focus {
            outline: none;
          }
        }
        button {
          border: none;
          margin-left: 0.3em;
          background-color: #f5f8f5;
          color: $fade;
          position: relative;
          @include align-center();
          margin: 1em auto;
          .send {
            margin-left: 0.3em;
          }
        }
      }
    }
    img {
      width: 60%;
      margin: auto;
    }
  }
}
//--------------Reset Password-----------------
.reset-password-page {
  background: #c9d6ff;
  background: -webkit-linear-gradient(to right, #e2e2e2, #c9d6ff);
  background: linear-gradient(to right, #e2e2e2, #c9d6ff);
  .container {
    width: auto;
    flex-direction: column;
    background-color: #0597cf;
    height: auto;
    padding: 2em 1em;
    box-shadow: 30px 30px 80px rgba(0, 0, 0, 0.267);
    .image-container {
      width: 65%;
    }
    header {
      text-align: center;
      color: $smoke;
      p {
        color: $smoke;
      }
      .input-div {
        .my-form {
          width: 100%;
          margin: 0;
          text-align: left;
        }
        button {
          margin: auto;
          width: 150px;
          @include flex-center();
        }
      }
    }
    .image-container {
      height: 100%;
      @include flex-center();
    }
  }
}
//------------------- Full Blog Page Stylings -----------------------------
.blog-details {
  padding: 2em 0;
  img {
    width: 100%;
  }
  header {
    padding: 1em 0;
    text-align: center;
    .profile-img {
      height: 80px;
      width: 80px;
      border-radius: 50%;
      margin: 0 auto 1em auto;
      background-image: url('../assets/student2.jpg');
      background-size: cover;
      background-position: center;
    }
    h4 {
      margin: 0;
    }
  }
  hr {
    width: 80%;
    margin: auto;
    opacity: 0.5;
  }
  .blog-content {
    h1 {
      color: $fade;
    }
    h4 {
      color: $fade;
    }
    p {
      color: $fade;
      font-size: 1.2em;
      line-height: 1.6;
      text-align: justify;
    }
  }
}
