@media (max-width: $mobile-break-point) {
  //-----global
  section {
    margin: 0.5em auto;
    padding: 0.5em;
  }
  .left-circle-border {
    height: 100px;
    width: 100px;
    left: -2%;
    opacity: 0;
  }
  .circle-design {
    height: 50px;
    width: 50px;
    bottom: -20%;
    left: -3%;
    opacity: 0;
  }
  .nav-bar {
    .navbar-logo {
      @include align-center();
      img {
        width: 100px;
      }
    }
    .menu-icon {
      opacity: 1;
    }
    .item-container {
      flex-direction: column;
      width: 100%;
      height: auto;
      padding: 1em 0;
      position: absolute;
      top: 50px;
      left: 0;
      display: none;
      z-index: -9999;
      transition: all 0.5s ease;
      .nav-items {
        text-align: center;
        margin-bottom: 1em;
        font-size: 1.1em;
      }
    }
    .item-container.active {
      background-color: $white;
      left: 0;
      display: block;
      z-index: 999;
      transition: all 0.5s ease;
      .nav-items {
        .drop-down {
          width: 200px;
          top: 150%;
          background-color: $white;
          z-index: 999;
          text-align: left;
